    flex-direction: column;
    justify-content: space-around;
    height:75%;
    width: 100%;
    /* display: flex;
    flex-direction: column;
    margin-top: 5rem; */

}





.Audio{
    --primary: #FFFFFC;
   

    display: flex;
    /* border:1px solid black; */
    background-color: #2e282a;
    border-radius: 14px;
    /* position: relative; */
    width: 100%;
    /* height: 100%; */
    padding: 1rem;
    align-items: center;
    justify-content: space-evenly;
    /* justify-content: center;  */
