}

.RightSection{
    display: flex;
    flex-direction: column;
    width:600px;

}

.RightSection h1, p{
    margin: 0 0.5rem;
    color: #FFFFFC
}

.PlayerFlex{
    display: flex;
    align-items:center;
    width:100%;
}
