.TimeFlex{
    display: flex;
    align-items:center;
    margin: 0 0.5rem;
    justify-content:space-between;
    width: 100%;
}

.TrackArt{
    max-width: 100%;
    border-radius: 15px;
    padding: 0 1.0rem;
}

.Title{
    display: flex;
    /* flex-direction: column; */
    /* font-size: 10rem; */
    justify-content: center;
    align-items: center;
    margin-right: 5rem;
    /* background-color: red; */
    /* height: 200px; */
    
}

.forwardBackward{
    display: flex;
    align-items: center;
    background: none;
    margin: 0 0.5rem;
    border: none;
    font-family: monospace;
    font-size: 15px;
    cursor: pointer;
    color: #FFFFFC;
}

.forwardBackward:hover{
    color: var(--primary)
}

.skipNext{
    display: flex;
    justify-content: center;
    align-items:center;
    width: 35px;
    height: 20px;
    margin:0 1rem;
    border-radius: 15px;
    border: none;
    background-color:  #FFFFFC;;
    color:#2E282A;
    cursor: pointer;
}

.skipNext:hover{
    opacity: 0.8;

}

.playPause{
    background-color: transparent;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 32px;
    color:var(--primary);
    display: flex;
    justify-content: center;
    align-items: center;

}

.currentTime,
.duration{
    font-family: monospace;
    font-size: 16px;

}

.currentTime{
    margin-right: 1rem;
    
    color: #FFFFFC;
}

.duration{
    margin-right: 1.5rem;
    color: #FFFFFC;
}
.progressBar{
    --bar-bg:#FFFFFC;
    --seek-before-width: 0;
    --seek-before-color:#423f39;
    --knobby: #425f37;
    --selectedKnobby: #525f37;

    appearance: none;
    background: var(--bar-bg);
    border-radius: 10px;
    position: relative;
    /* width: 100%; */
    height: 15px;
    width: 100%;
    outline: none;
}

.progressBar input{
    width: 100%;
}

/* progress bar - safari */

.progressBar ::-webkit-slider-runnable-track{
    background: var(--bar-bg);
    border-radius: 10px;
    position: relative;
    width: 100%;
    height: 15px;
    outline: none;

}
/* progress bar - firefox */

.progressBar ::-moz-range-track{
    background: var(--bar-bg);
    border-radius: 10px;
    position: relative;
    width: 100%;
    height: 15px;
    outline: none;

}

.progressBar ::-moz-focus-outer{
    border: 0;
}

.progressBar::before{
    content: '';
    height: 15px;
    width: var(--seek-before-width);
    background-color: var(--seek-before-color);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
}

.progressBar::-moz-range-progress{
    background-color: var(--seek-before-color);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    height: 15px;
}

.progressBar ::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 15px;
    width: 20px;
    border-radius: 50%;
    border: none;
    background-color: var(--knobby);
    cursor: pointer;
    position: relative;
    margin:-1px 0 0 0;
    z-index: 3;
    box-sizing: border-box;
}

.progressBar:active::-webkit-slider-thumb{
    transform: scale(2.0);
    background: var(--selectedKnobby);
}

.progressBar::-moz-range-thumb{
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border: transparent;
    background-color: var(--knobby);
    cursor: pointer;
    position: relative;
    z-index: 3;
    box-sizing: border-box;
}

.progressBar:active::-moz-range-thumb{
    transform: scale(1.2);
    background: var(--selectedKnobby);
}